.hero {
  z-index: -2;
  background: linear-gradient(to bottom, #7EACB5, #ebdbcc);
  height: 45rem;
  overflow: hidden;
}
.hero .hr1 {
  margin: 0 0 0 0;
  height: 16px;
  border: 0;
  background-color: #eb7e36;
}
.hero .hr2 {
  margin: 0 0 0 0;
  height: 8px;
  border: 0;
  background-color: #df4545;
}
.hero .hr3 {
  margin: 0 0 0 0;
  height: 8px;
  border: 0;
  background-color: #2e1c18;
}
@media screen and (max-width: 500px) {
  .hero {
    height: 35rem;
  }
}
.hero .hero_contents {
  height: 45rem;
  font-family: "hand_written";
  font-weight: 700;
  font-size: 1.25rem;
  letter-spacing: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}
@media screen and (max-width: 500px) {
  .hero .hero_contents {
    height: 35rem;
    font-size: 1rem;
  }
}
.hero .hero_contents .hero_item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero .hero_contents .hero_item .clouds_fluffy {
  z-index: 0;
  position: absolute;
  animation: fluffy_cloud_movement 120s linear infinite;
  animation-direction: alternate;
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .clouds_fluffy {
    animation: fluffy_cloud_movement 20s linear infinite;
  }
}
.hero .hero_contents .hero_item .clouds_regular {
  z-index: 0;
  position: absolute;
  animation: regular_cloud_movement 120s linear infinite;
  animation-direction: alternate;
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .clouds_regular {
    animation: regular_cloud_movement 20s linear infinite;
  }
}
.hero .hero_contents .hero_item .flower_logo {
  z-index: 0;
  position: absolute;
  height: auto;
  width: 40rem;
  animation: rotate 80s linear infinite;
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .flower_logo {
    width: 32rem;
  }
}
.hero .hero_contents .hero_item .text {
  z-index: 0;
}
@media screen and (max-width: 800px) {
  .hero .hero_contents .hero_item .text {
    text-align: center;
  }
}
.hero .hero_contents .hero_item .logo {
  width: 40rem;
  margin-bottom: 1rem;
  animation: sinusoid 3s ease-in-out infinite;
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .logo {
    width: 24rem;
  }
}
.hero .hero_contents .hero_item .img_roto {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
.hero .hero_contents .hero_item .img_roto .link:hover {
  z-index: 3;
}
.hero .hero_contents .hero_item .img_roto .rtg_album {
  z-index: 1;
  width: 9rem;
  transition: transform 0.3s ease, filter 0.3s ease, opacity 0.3s ease;
  opacity: 1;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
  filter: grayscale(0.8);
}
.hero .hero_contents .hero_item .img_roto .rtg_album:hover {
  z-index: 3;
  transform: scale(2);
  opacity: 1;
  filter: grayscale(10%);
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .img_roto .rtg_album {
    width: 6rem;
    filter: grayscale(10%);
  }
}
.hero .hero_contents .hero_item .img_roto .album {
  z-index: 0;
  width: 16rem;
  transition: transform 0.3s ease, filter 0.3s ease, opacity 0.3s ease;
  opacity: 1;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
  filter: grayscale(0.8);
}
.hero .hero_contents .hero_item .img_roto .album:hover {
  z-index: 3;
  transform: scale(1.1);
  opacity: 1;
  filter: grayscale(10%);
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .img_roto .album {
    width: 12rem;
    filter: grayscale(10%);
  }
}
.hero .hero_contents .hero_item .img_roto .sp_album {
  z-index: 1;
  width: 9rem;
  transition: transform 0.3s ease, filter 0.3s ease, opacity 0.3s ease;
  opacity: 1;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
  filter: grayscale(0.8);
}
.hero .hero_contents .hero_item .img_roto .sp_album:hover {
  z-index: 3;
  transform: scale(2);
  opacity: 1;
  filter: grayscale(10%);
}
@media screen and (max-width: 500px) {
  .hero .hero_contents .hero_item .img_roto .sp_album {
    width: 6rem;
    filter: grayscale(10%);
  }
}

@keyframes sinusoid {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translate(0, 10px);
  }
  100% {
    transform: translate(0);
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes fluffy_cloud_movement {
  0% {
    transform: translate(-1500px);
  }
  100% {
    transform: translate(1500px);
  }
}
@keyframes regular_cloud_movement {
  0% {
    transform: translate(1500px);
  }
  100% {
    transform: translate(-1500px);
  }
}
@media screen and (max-width: 500px) {
  @keyframes fluffy_cloud_movement {
    0% {
      transform: translate(-500px);
    }
    100% {
      transform: translate(500px);
    }
  }
}
@media screen and (max-width: 500px) {
  @keyframes regular_cloud_movement {
    0% {
      transform: translate(500px);
    }
    100% {
      transform: translate(-500px);
    }
  }
}
