@font-face {
  font-family: "hand_written";
  src: url("../assets/fonts/Providence-Sans.otf") format("opentype");
}
@font-face {
  font-family: "frog";
  src: url("../assets/fonts/SuperFrog-Yqy1q.ttf") format("opentype");
}
.about-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  overflow: hidden;
}
.about-header .about-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 8rem;
  width: 95%;
  max-width: 2000px;
}
.about-header .about-nav .about-logo {
  align-self: center;
  width: 25%;
}
.about-header .about-nav .about-logo img {
  height: auto;
  width: 24rem;
  object-fit: cover;
}
@media screen and (max-width: 500px) {
  .about-header .about-nav .about-logo img {
    width: 12rem;
  }
}
.about-header .about-nav .about-center-logo {
  width: 25%;
}
.about-header .about-nav .about-center-logo img {
  height: auto;
  width: 29rem;
}
@media screen and (max-width: 1100px) {
  .about-header .about-nav .about-center-logo img {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  .about-header .about-nav .about-center-logo {
    width: 0;
  }
}
.about-header .about-nav .about-home-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
}
.about-header .about-nav .about-home-button .home-button {
  width: 12rem;
  height: 4rem;
  outline: none;
  border: 3px solid #2e1c18;
  background-color: #ebdbcc;
  box-shadow: 4px 4px 5px #2e1c18;
  transition: box-shadow 0.3s ease;
}
.about-header .about-nav .about-home-button .home-button:hover {
  box-shadow: 8px 8px 5px #2e1c18;
}
.about-header .about-nav .about-home-button .home-button a {
  font-family: "hand_written", sans-serif;
  font-weight: bolder;
  font-size: 1.5rem;
  color: #2e1c18;
  text-decoration: none;
}
@media screen and (max-width: 500px) {
  .about-header .about-nav .about-home-button .home-button {
    width: 9rem;
  }
}

.about-container {
  width: 100%;
  background: linear-gradient(to bottom, #eb9191, #ebdbcc);
}
.about-container .hr1 {
  margin: 0 0 0 0;
  height: 16px;
  border: 0;
  background-color: #eb7e36;
}
.about-container .hr2 {
  margin: 0 0 0 0;
  height: 8px;
  border: 0;
  background-color: #df4545;
}
.about-container .hr3 {
  margin: 0 0 0 0;
  height: 8px;
  border: 0;
  background-color: #2e1c18;
}
.about-container .about-item-grid {
  display: grid;
  grid-template-columns: 200px 200px;
  grid-template-rows: 200px 200px 200px 200px;
  gap: 1rem;
  grid-template-areas: "box1 box1" "box1 box1" "box2 box3" "box2 box4";
}
@media screen and (max-width: 500px) {
  .about-container .about-item-grid {
    grid-template-columns: 150px 150px;
    grid-template-rows: 150px 150px;
    grid-template-areas: "box1 box2" "box3 box4";
  }
}
.about-container .about-item-grid .image1 {
  clip-path: polygon(20% 0%, 80% 0%, 100% 20%, 100% 80%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
}
.about-container .about-item-grid .image3 {
  clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
}
.about-container .about-item-grid .image2 {
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
}
.about-container .about-item-grid .image4 {
  border-radius: 100%;
}
.about-container .about-item-grid .about-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-container .about-item {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: wrap;
  margin: 0 auto;
  padding: 6rem 0rem 3rem 0rem;
  width: 75%;
}
@media screen and (max-width: 500px) {
  .about-container .about-item {
    width: 90%;
  }
}
.about-container .about-item .about-item-image {
  width: 50%;
}
.about-container .about-item h1 {
  font-family: "frog", serif;
  font-weight: 400;
  font-size: 2.5rem;
  color: #2e1c18;
}
@media screen and (max-width: 500px) {
  .about-container .about-item h1 {
    padding-top: 2rem;
  }
}
.about-container .about-item .about-item-content {
  width: 50%;
  font-family: "hand_written", serif;
  font-weight: 400;
  font-size: 1.5rem;
  color: #2e1c18;
}
@media screen and (max-width: 500px) {
  .about-container .about-item .about-item-content {
    width: 100%;
    text-align: start;
  }
}
.about-container .about-item .about-item-content p {
  font-size: 1.25rem;
}

.bibliography {
  width: 60%;
  max-width: 2000px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  margin: 0 auto;
  font-family: "hand_written", serif;
  font-size: 1.25rem;
}
.bibliography h1 {
  font-family: "frog", serif;
  font-size: 2.5rem;
}
@media screen and (max-width: 500px) {
  .bibliography {
    width: 90%;
  }
}

.poster-container {
  width: min(1000px, 100%);
  margin: 8rem auto;
  columns: 3 300px;
  column-gap: 1rem;
}
@media screen and (max-width: 950px) {
  .poster-container {
    width: min(1000px, 90%);
    columns: 2 50px;
  }
}
.poster-container img {
  display: block;
  margin-bottom: 1rem;
  width: 100%;
}

.place-holder-grad {
  height: 200px;
  background: linear-gradient(to bottom, #ebdbcc, #2e1c18);
}